@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+JP&display=swap');

* {
    box-sizing: border-box;
    margin: 0;
    color: #D9D4CB;
}

body {
    font-size: 16px;
    background-color: #252422;
    font-family: 'Noto sans JP', sans-serif;
}

.menu-container {
    background-color: #403D39;
    border-radius: 10px;
    margin: 1rem 0.5rem;
    padding: 1rem;
    color: black;
    overflow: hidden;
    flex-grow: 1;
    flex-basis: auto;
    width: 400px;
}

.restaurant-box {
    display: flex;
    flex-wrap: wrap;
}


.subheader {
    margin-bottom: 5px;
    padding-bottom: 5px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-bottom: 1px solid #CCC5B9;
}

.subheader span {
    margin-right: 1rem;

}

.content p{
    margin-bottom: 5px;
}

.restaurant-name{
    margin-bottom: 10px;

}

.restaurant-name h2{
    color: #D9D4CB;
    font-size: 2em;
}

.info{
    margin-left: 1rem;
}

footer{
    font-size: 10px;
}


